
body {
  line-height: 1.2; /* 设置行间距为1.2 ,默认是1.6的太大*/
}
.tab-swiper {
  background-color: #fff;
  text-align: center;

  padding-top: 10px;
}
.txtLeft {
  display: -webkit-box;
  display: -webkit-flex;
  display: flex;
  white-space: nowrap;
  -webkit-box-pack: start;
  -webkit-justify-content: flex-start;
          justify-content: flex-start; /* | flex-end | center | space-between | space-around; */
  /*主轴对齐方式：左对齐（默认） | 右对齐 | 居中对齐 | 两端对齐 | 平均分布*/
}
.txtRight {
  color: aqua;
  display: -webkit-box;
  display: -webkit-flex;
  display: flex;
  white-space: nowrap;
  -webkit-box-pack: end;
  -webkit-justify-content: flex-end;
          justify-content: flex-end; /* flex-end | center | space-between | space-around; */
  /*主轴对齐方式：左对齐（默认） | 右对齐 | 居中对齐 | 两端对齐 | 平均分布*/
}



