
.boxLeft {
  -webkit-box-orient: horizontal;
  -webkit-box-direction: normal;
  -webkit-flex-direction: row;
          flex-direction: row;
  -webkit-box-flex: 4;
  -webkit-flex: 4;
          flex: 4;
}
.boxRight {
  -webkit-box-flex: 1;
  -webkit-flex: 1;
          flex: 1;
  -webkit-box-pack: end;
  -webkit-justify-content: flex-end;
          justify-content: flex-end; /*水平居中*/
  -webkit-box-align: center;
  -webkit-align-items: center;
          align-items: center; /*垂直居中*/
  display: -webkit-box;
  display: -webkit-flex;
  display: flex;
  margin-right: 14px;
}
.boxTitle {
  height: 30px;
  line-height: 30px;
  width: 80px;
  display: inline-block;
  text-align: justify;
}
.boxTitle:after {
  content: "";
  display: inline-block;
  width: 100%;
}
.boxHead {
  display: -webkit-box;
  display: -webkit-flex;
  display: flex;
  padding: 5px 10px;
}
.boxContent {
  display: -webkit-box;
  display: -webkit-flex;
  display: flex;
  padding-left: 20px;
  color: #999;
}



