
.boxRow {
  display: -webkit-box;
  display: -webkit-flex;
  display: flex;
  -webkit-box-pack: center;
  -webkit-justify-content: center;
          justify-content: center; /*水平居中*/
  -webkit-box-align: center;
  -webkit-align-items: center;
          align-items: center; /*垂直居中*/
  font-size: 14px;
  width: 100%;
  height: 30px;
}
.importentInfo {
  color: black;
  font-weight: bold;
  font-family: "仿宋";
}
.boxHead {
  display: -webkit-box;
  display: -webkit-flex;
  display: flex;
  -webkit-box-flex: 1;
  -webkit-flex: 1;
          flex: 1;
}
.boxTitle {
  -webkit-box-flex: 1;
  -webkit-flex: 1;
          flex: 1;
  display: -webkit-box;
  display: flex;
  display: -webkit-flex;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  -webkit-box-pack: center;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
  -webkit-flex-direction: column;
          flex-direction: column;
}
.boxContent {
  display: inline-block;
  -webkit-box-pack: start;
  -webkit-justify-content: flex-start;
          justify-content: flex-start; /*水平居中*/
  padding-left: 8px;
  /* padding-left: 8px; */
  color: #999;
  -webkit-box-flex: 2;
  -webkit-flex: 2;
          flex: 2;
}



